<template>
	<view>
		<view class="center">
			<input type="text" :focus="focus1" v-model='value1' class="now" maxlength="1" @input="inputChange(1, $event)"/>
			<input type="text" :focus="focus2" v-model='value2' class="now" maxlength="1" @input="inputChange(2, $event)"/>
			<input type="text" :focus="focus3" v-model='value3' class="now" maxlength="1" @input="inputChange(3, $event)"/>
			<input type="text" :focus="focus4" v-model='value4' class="now" maxlength="1" @input="inputChange(4, $event)"/>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				value1: '',
				value2: '',
				value3: '',
				value4: '',
				
				focus1: true,
				focus2: false,
				focus3: false,
				focus4: false,
				
				tmpIndex: 1,
			}
		},
		onReady() {
			
			
		},
		methods: {
			initStatus() {
				this.focus1 = false
				this.focus2 = false
				this.focus3 = false
				this.focus4 = false
			},
			inputChange(index, e) {
				let value = e.detail.value
				if (value.length > 0) {
					this.tmpIndex++
				} else {
					this.tmpIndex--
				}
				if (this.tmpIndex > 4) { this.tmpIndex = 4}
				if (this.tmpIndex < 1) { this.tmpIndex = 1}
				this.initStatus()
				if (this.tmpIndex == 1) {
					this.focus1 = true
				} else if (this.tmpIndex == 2) {
					this.focus2 = true
				} else if (this.tmpIndex == 3) {
					this.focus3 = true
				} else if (this.tmpIndex == 4) {
					this.focus4 = true
				}
			}
			
		}
	}
</script>

<style lang="less">
	.center {
		display: flex;
		width: 100%;
		height: 500rpx;
		margin:  0 auto;
		input {
			flex: 1;
			height: 100rpx;
			font-size: 40rpx;
			text-align: center;
		}
		.now {
			width: 70rpx;
			height: 70rpx;
			border: 2rpx #ccc solid;
		}
		.now:focus {
			border: 2rpx red solid;
		}
	}
	
</style>
